<script>
export default {
  name: "LayoutPage",
  created() {
    console.log("sfsdf");
  },
  data() {
    return {
      currentPage: "dashboard",
      isnavdrop: false,
      form: {
        email: "",
        name: "",
        password: "",
        cpassword: "",
        phone: "",
      },
      isformshow: true,
    };
  },
  methods: {
    navdrop() {
      this.isnavdrop = !this.isnavdrop;
    },
    clickLogin() {
      alert("df");
      this.$bvModal.hide("modal-signup");
      this.$bvModal.show("modal-login");
    },
    clickSignup() {},
    onSubmit() {},
  },
  watch: {
    $route: {
      deep: true,
      handler: function (page) {
        this.currentPage = page.name;
      },
    },
  },
};
</script>